/* ✅ src/components/Cover.css */
.cover-wrapper {
  position: relative;
  width: 100%;
  max-width: 430px;
  margin: 0 auto;
  overflow: hidden;
  transition: all 1s ease-in-out;
}

.cover-wrapper.intro {
  height: 100vh;
}

.cover-wrapper.scrolled {
  height: 50vh;
}

.cover-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(4, 1fr);
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 1.5s ease-in-out;
}

.cover-grid.fade-in {
  opacity: 1;
}

.grid-cell {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.grid-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: grayscale(100%);
  display: block;
}

.cover-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: rgba(0, 0, 0, 0.4);
  z-index: 1;
}

.cover-text {
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.cover-text h1 {
  font-size: 32px;
  font-family: 'Nanum Pen Script', cursive;
  white-space: nowrap;
  overflow: hidden;
  animation: typing 4s steps(40, end) forwards;
}

@keyframes typing {
  from { width: 0; }
  to { width: 100%; }
}
.typing-text {
  font-family: 'Nanum Pen Script', cursive;
  font-size: 36px;
  color: #fff;
  line-height: 1.6;
  text-align: center;
  white-space: pre-line; /* 줄바꿈(\n) 자동 인식 */
}
